<script>
export default {
  data: () => ({
    viewport: 'about',
    sections: {}
  }),
  created () {
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  mounted () {
    this.sections.about = document.getElementById('about')
    this.sections.skills = document.getElementById('skills')
    this.sections.projects = document.getElementById('projects')
  },
  methods: {
    handleScroll (event) {
      if (window.scrollY >= this.sections.skills.offsetTop + this.sections.skills.offsetHeight) {
        this.viewport = 'projects'
        return
      }
      if (window.scrollY >= this.sections.about.offsetTop + this.sections.about.offsetHeight) {
        this.viewport = 'skills'
        return
      }
      this.viewport = 'about'
    }
  }
}
</script>
